.about {
  &__content {
    height: 100%;
    padding: 10rem 0;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    align-items: center;
    justify-content: start;
    justify-items: start;
    grid-gap: 3rem;
  }

  &__info {
    grid-column: 1/2;
    display: grid;
    grid-auto-flow: row;
    grid-gap: 4rem;

    &__title {
      grid-area: 1/1/2/2;
      font-weight: 900;
      font-size: var(--text-45);
      line-height: 5.6rem;
      text-transform: uppercase;
      position: relative;
      &::before {
        content: "";
        color: var(--dark);
        width: 7rem;
        height: 0.6rem;
        background-color: var(--dark);
        position: absolute;
        top: 0.6em;
        right: calc(100% + 2rem);
      }
    }
    &__description {
      font-weight: 250;
      font-size: var(--text-18);
      line-height: 2.8rem;
    }
    &__subtitle {
      font-weight: 500;
      font-size: var(--text-24);
      line-height: 2.8rem;
      color: var(--blue);
    }
  }
  &__advantages {
    grid-column: 2/3;
    height: calc(100% + 6rem);

    display: grid;
    grid-template: repeat(2, 50%) / repeat(2, 50%);
    align-items: start;
    column-gap: 3rem;

    .advantage {
      display: grid;
      grid-auto-flow: row;
      height: 26rem;
      background-color: var(--gray);
      row-gap: 1.5rem;
      padding: 3rem;
      &__icon {
        svg {
          width: 5rem;
          height: 5rem;
        }
      }
      &__title {
        font-weight: 500;
        font-size: var(--text-20);
        line-height: 2.1rem;
      }
      &__description {
        font-weight: 300;
        font-size: var(--text-14);
        line-height: 1.9rem;
      }

      &:nth-child(1),
      &:nth-child(3) {
        align-self: end;
      }
    }
  }
}
